@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,400;0,500;1,400;1,500&display=swap);
body {
  margin: 0;
  font-family: 'Josefin Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;

  background: #9d50bb; /* fallback for old browsers */
  background: linear-gradient(to right, #9d50bb, #6e48aa); 
  background: -webkit-linear-gradient(to right, #9d50bb, #6e48aa); /* Chrome 10-25, Safari 5.1-6 */

}

h1{
  text-transform: uppercase;
  font-style: italic;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.calc {
    height: 320px;
    width: 235px;
    border-radius: 5px;
    overflow: hidden;
    display: grid;
    grid-template-columns: repeat(4, 25%);
    grid-template-rows: 1fr repeat(5, 48px);
}
:root{
    --bg-button: #f0f0f0;
    --border-button: solid 1px #888;
    --bg-operator: #ff7b00;
}

.button{
    font-size: 1.4em;
    font-family: 'Josefin Sans', sans-serif;
    font-weight: 400;
    border: none;

    background-color: #f0f0f0;

    background-color: var(--bg-button);
    border-right: solid 1px #888;
    border-right: var(--border-button);
    border-bottom: solid 1px #888;
    border-bottom: var(--border-button);

    outline: none;
}

.operator {
    background-color: #ff7b00;
    background-color: var(--bg-operator);
    color: white;
}

.button:active{
    background-color: #ccc;
}

.button.operator:active{
    background-color: #1900ffaa;
}

.triple{
    grid-column: span 3;
}

.double{
    grid-column: span 2;
}
.display{
    grid-column: span 4;
    background-color: rgba(0, 0, 0, 0.76);

    font-size: 2em;

    display: -webkit-flex;

    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
    padding: 10px;
    color: white;
}
